import React, { useState } from 'react'
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Tableval from './Tableval';
const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));


const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

const Example = () => {
    const [studentdata,setStudentdata] = useState([]);
    const getStudent =async ()=>{
      let student_name = "rj503@mail.com";
      let bbody = JSON.stringify({student_name});
        // const response = await fetch(`http://localhost:3000/api/Students/studentfetch`, {
            const response = await fetch(`https://naughty-pullover-fish.cyclic.app/api/Students/studentfetch`, {
          method: 'PUT',
         
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
          },
           body: JSON.stringify({student_name}) 
        });
        let a = await response.json();
        console.log("the body is");
        console.log(bbody);
        console.log("the data is");
        console.log(a);
        setStudentdata(a);
   };
   
   getStudent();
  return (
    <>
    
    <TableContainer component={Paper}>
                <Table sx={{ minWidth: 700 }} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>Dessert (100g serving)</StyledTableCell>
                            <StyledTableCell align="right">Calories</StyledTableCell>
                            <StyledTableCell align="right">Fat&nbsp;(g)</StyledTableCell>
                            <StyledTableCell align="right">Carbs&nbsp;(g)</StyledTableCell>
                            <StyledTableCell align="right">Protein&nbsp;(g)</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {studentdata.map((row) => (
                            <StyledTableRow key={row.student_name}>
                                <StyledTableCell component="th" scope="row">
                                    {row.student_name}
                                </StyledTableCell>
                                <StyledTableCell align="right">{row.Address}</StyledTableCell>
                                <StyledTableCell align="right">{row.Phone}</StyledTableCell>
                                <StyledTableCell align="right">{row.Total_payment}</StyledTableCell>
                                <StyledTableCell align="right">{row.__v}</StyledTableCell>
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
     </>
  )
}

export default Example
