import { Box, Button, IconButton, Snackbar } from '@mui/material'
import React, { useState } from 'react'

import CloseIcon from '@mui/icons-material/Close';
const UpdateClass = () => {

  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState("update Success");

  const handleClick = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const action = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );



  const increase = async (e) => {
    console.log("Increaser clicked");
    e.preventDefault();
    const options = {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    };

    try {
      // const response = await fetch("http://localhost:3001/api/Students//increaseClass", options);
      const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Students/increaseClass", options);
      const json = await response.json();
      console.log(json);
      if (json.success) {
        setOpen(true);
      }
    } catch (error) {
      console.error(error);
      setMessage("update Failed");
      setOpen(true);
    }
  }

  const decrease = async (e) => {
    console.log("Decrease clicked");
    e.preventDefault();
    const options = {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    };

    try {
      // const response = await fetch("http://localhost:3001/api/Students/decreaseClass", options);
      const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Students/decreaseClass", options);
      const json = await response.json();
      console.log(json);
      if (json.success) {
        setOpen(true);
      }
    } catch (error) {
      console.error(error);
      setMessage("update Failed");
      setOpen(true);
    }
  }
  const updatePayment = async (e) => {

    e.preventDefault();

    const grade = document.getElementById("paymentGrade").value;
    const Total_payment = document.getElementById("paymentTotal").value;

    const data = { grade, Total_payment };
    const options = {
      method: "PUT",
      body: JSON.stringify(data),
      headers: { "Content-Type": "application/json" },
    };

    try {
      // const response = await fetch("http://localhost:3001/api/Students/updatePayment", options);
      const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Students/updatePayment", options);
      const json = await response.json();
      console.log(json);
      if (json == false) {
        console.log(json);

        window.alert("Update Failed");


      }
      else {

        window.alert("Payment Updated");
      }
    } catch (error) {
      console.error(error);
      window.alert("Update Failed");

    }

    console.log("h");
  }


  return (
    <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "space-around" }}>

      <Button variant="contained" onClick={increase}>Increase class</Button>
      <form className='spsf' onSubmit={updatePayment}>
        <input required className='inputcss' type='Number' placeholder='Grade' id="paymentGrade" />
        <input required className='inputcss' type='number' placeholder='Total payment' id="paymentTotal" />
        <input type='submit' className='buttoncss' />
      </form>
      <Button variant="contained" onClick={decrease}>Decrease class</Button>




      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message={message}
        action={action}
      />
    </Box>
  )
}

export default UpdateClass
