import { Box, Button, Card, IconButton, Input, TextField } from '@mui/material'
import React, { useState } from 'react'
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import Snackbar from '@mui/material/Snackbar';
import CloseIcon from '@mui/icons-material/Close';
const AddtheStudent = () => {

  const [open, setOpen] = useState(false);
  const [phoneHelpertext, setPhoneHelperText] = useState("Please enter Phone number");
  const [gradeHelperText, setGradeHelperText] = useState("Please enter Grade level");

  const handleClick = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const action = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );

  let history = useNavigate();
  useEffect(() => {
    if (localStorage.getItem('token')) {
      console.log("toiken available")
    }
    else {
      history("/login");
    }
    // eslint-disable-next-line
  }, []);

  const handleSubmit = async (e) => {
    console.log("clickd")
    e.preventDefault();
    const student_name = document.getElementById("studentName").value;
    const Address = document.getElementById("address").value;
    const Phone = document.getElementById("phone").value;
    const grade = document.getElementById("grade").value;
    const Total_payment = document.getElementById("totalPayment").value;

    const data = { student_name, Address, Phone, grade, Total_payment };
    const options = {
      method: "POST",
      body: JSON.stringify(data),
      headers: { "Content-Type": "application/json" },
    };
    if (Phone.length != 10) {
      setPhoneHelperText("Enter Phone No of length 10");
      window.alert("Enter Phone No of length 10 digit");
    } else {
      if (grade < 5 || grade > 10) {
        setGradeHelperText("The garde must be between 5-10");
        setPhoneHelperText("Filled");
        window.alert("The garde must be between 5-10");

      } else {
        setGradeHelperText("Filled");
        try {
          // const response = await fetch("http://localhost:3001/api/Students/create", options);
          const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Students/create", options);
          const json = await response.json();
          console.log(json);
          if (json) {
            setOpen(true);
          } else {
            window.alert("Failed");
          }
          // setOpen(true);
        } catch (error) {
          
          console.error(error.message);
          
          window.alert("Error,Might be student with same name and number already exist");
          
        }
      }


    }

  };

  return (
    <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center" }} >

      <form onSubmit={handleSubmit} style={{ "width": "100%", "margin": "50px", "display": "flex", "align-items": "center", "justify-content": "center" }}>
        <Card variant="outlined" sx={{ width: "50%", borderRadius: "16px" }}>
          <Box sx={{ backgroundColor: "skyblue", color: "white" }}> <h2>&nbsp;Student Form</h2></Box>
          <TextField required helperText="Please enter your name" fullWidth label="Student Name" variant="filled" id="studentName" margin="dense" />
          <TextField required helperText="Please enter your Address" fullWidth label="Address" variant="filled" id="address" margin="dense" />
          <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
            <TextField required type='number' sx={{ width: "48%" }} helperText={phoneHelpertext} label="Phone" id="phone" variant="filled" margin="dense" />
            <TextField required type='number' sx={{ width: "48%" }} helperText={gradeHelperText} label="Grade" id="grade" variant="filled" margin="dense" />

          </Box>
          <Box sx={{ display: "flex" }}>
            <TextField required type='number' sx={{ width: "48%" }} helperText="Please enter Total Fee" label="Total-Payment" id="totalPayment" variant="filled" margin="dense" />
          </Box>
          <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
            <Button sx={{ width: "90%", margin: "20px" }} variant="contained" type="submit"><h3>Submit</h3></Button>
          </Box>
        </Card>
      </form>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message="Student added"
        action={action}
      />
    </Box>
  )
}

export default AddtheStudent
