import { Box, Button, Card, IconButton, Snackbar, TextField } from '@mui/material';
import React, { useState } from 'react'
import { useEffect } from 'react';
import {useNavigate} from 'react-router-dom';
import CloseIcon from '@mui/icons-material/Close';


const Passwordchange = () => {

    let history = useNavigate();
  useEffect(()=>{
    if(localStorage.getItem('token'))
    {
        console.log("toiken available")}
    else{
        history("/login");
    }
        // eslint-disable-next-line
    },[]);

    const [open, setOpen] = useState(false);


    const handleClose = () => {
        setOpen(false);
    };

    const action = (
        <React.Fragment>
            <Button color="secondary" size="small" onClick={handleClose}>
                UNDO
            </Button>
            <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={handleClose}
            >
                <CloseIcon fontSize="small" />
            </IconButton>
        </React.Fragment>
    );

    const onSubmitdata = async (e) => {
        e.preventDefault();
        const name = document.getElementById("cname").value;
        const password = document.getElementById("cpassword").value;
        
        const newpassword = document.getElementById("changedpassword").value;
        // const response = await fetch("http://localhost:3001/api/Admin/Passwordchange", {
            
        const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Admin/Passwordchange", {
            method: 'PUT',

            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            },
            body: JSON.stringify({ Admin_name: name, Password: password,NewPassword: newpassword }) // body data type must match "Content-Type" header
        });
        const json = await response.json();
        console.log(json);
        if (json.success) {

            window.alert("Password changed");
        }
        if (!json.success) {
            setOpen(true);
            console.log("wrong credentials")
        }



    }

    return (
        <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center" }} >
            <form onSubmit={onSubmitdata}>
                <Card variant="outlined" sx={{ width: "100%", borderRadius: "16px" }}>
                    <Box sx={{ backgroundColor: "skyblue", color: "white" }}> <h2>&nbsp;Admin login</h2></Box>
                    <TextField required helperText="Please enter your name" fullWidth label="Admin Name" variant="filled" id="cname" margin="dense" />
                    <TextField required  inputProps={{ minLength: 6}} type="Password" helperText="Please enter your password" fullWidth label="Old Password" variant="filled" id="cpassword" margin="dense" />
                    <TextField required inputProps={{ minLength: 6}} type="Password" helperText="Please enter your new password" fullWidth label=" New Password" variant="filled" id="changedpassword" margin="dense" />

                    <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <Button sx={{ width: "90%", margin: "20px" }} variant="contained" type="submit"><h3>Submit</h3></Button>
                    </Box>
                </Card>
            </form>
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                message="wrong credentials"
                action={action}
            />
        </Box>

    )
}

export default Passwordchange
