import React, { useContext, useEffect, useState } from 'react'
import { Box, Button, Card, IconButton, Input, TextField } from '@mui/material'
import StudentContexr from '../Context/StudentContexr';
import {useNavigate} from 'react-router-dom';

const AddPayment = () => {
    const context = useContext(StudentContexr);
    const {studentUII,studentgrade,setStudenttotal,setStudentgrade,studentphone} = context;
    let history = useNavigate();
    useEffect(()=>{
        if(localStorage.getItem('token'))
        {
            console.log("toiken available")}
        else{
            history("/login");
        }
            // eslint-disable-next-line
        },[]);

    const onSubmitdata = async (e) => {
        e.preventDefault();
        const payment = document.getElementById("Addpayment").value;
        const paymentYear = document.getElementById("Addpaymentyear").value;
        // const response = await fetch("http://localhost:3001/api/Studentpayment/create", {
          const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Studentpayment/create", {
          method: 'POST',
    
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
          },
          body: JSON.stringify({ student_name: studentUII, year: paymentYear,Phone:studentphone,grade:studentgrade,Total_payment_done:payment}) // body data type must match "Content-Type" header
        // body: JSON.stringify({ student_name: "rajesh", year: "2222",Phone:2,grade:"2",Total_payment_done:2}) // body data type must match "Content-Type" header
        });

        const json = await response.json();
        console.log(json);
        if (json.success) {
            window.alert("Payment is Done");
            history("/about");
        //   give the alert
        }
        if(!json.success){

          window.alert("wrong credentials");
        }
    
    
    
      }

  return (
    <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center" }} >
        <form onSubmit={onSubmitdata}>
          <Card variant="outlined" sx={{ width: "100%", borderRadius: "16px" }}>
            <Box sx={{ backgroundColor: "skyblue", color: "white" }}> <h2>&nbsp;Add Payment for {studentUII}</h2></Box>
            <TextField required type='number' helperText="Please enter Total payment in Rs" fullWidth label="Total amount(Rs)" variant="filled" id="Addpayment" margin="dense" />
            <TextField required helperText="Please enter date of year" fullWidth label="yyyy-mm-dd" variant="filled" id="Addpaymentyear" margin="dense" />
            <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
              <Button sx={{ width: "90%", margin: "20px" }} variant="contained" type="submit"><h3>Submit</h3></Button>
            </Box>
          </Card>
        </form>
      </Box>
  )
}

export default AddPayment
