import { Box, Stack } from '@mui/material'
import React, { useState } from 'react'
import DRawer from './Component/DRawer'
// import Feed from './Component/Feed'
import Feeed from './Component/Feeed'
import Navbar from './Component/Navbar'
import Sidebar from './Component/Sidebar'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
} from "react-router-dom";
import Classses from './Component/Classses'
import Studentui from './Component/Studentui'
import Example from './Component/Example'
import AddtheStudent from './Component/AddtheStudent'
import Login from './Component/Login'
import Dashboard from './Component/Dashboard'
import Passwordchange from './Component/Passwordchange'
import AddAdmin from './Component/AddAdmin'
import AddPayment from './Component/AddPayment'
import Print from './Component/Print'
import UpdateClass from './Component/UpdateClass'
import ResultUI from './Component/Result/ResultUI'
import ResultData from './Component/Result/ResultData'
import StudentSearch from './Component/StudentSearch'
import StudentPaymentSearch from './Component/StudentPaymentSearch';
import AdminResultShow from './Component/Result/AdminResultShow'

const Ecom = () => {


  const [sidenav, setSidenav] = useState(false);
  const [ADrawer, setADrawer] = useState(true);



  return (
    <>
      <Router>
        {/* <DRawer ADrawer={ADrawer} setADrawer={setADrawer} /> */}
        <Box sx={{ md: 0 }}>
          <Navbar setADrawer={setADrawer} sidenav={sidenav} setSidenav={setSidenav} />
          <Stack
            direction={{ xs: 'row', sm: 'row' }}
            spacing={{ xs: 1, sm: 1, md: 1 }}
            justifyContent="space-between"
          >
            <Sidebar sidenav={sidenav} />
            
            <Routes>
              <Route exact path="/about" element={<Classses />} />
              <Route exact path="/classlist" element={<Feeed />} />
              <Route exact path="/student" element={<Studentui />} />
              <Route exact path="/Addstudent" element={<AddtheStudent />} />
              <Route exact path="/login" element={<Login />} /> 
              <Route exact path="/" element={<Dashboard />} /> 
              <Route exact path="/Passwordchange" element={<Passwordchange/>} /> 
              <Route exact path="/AddAdmin" element={<AddAdmin/>} /> 
              <Route exact path="/AddPayment" element={<AddPayment/>} /> 
              <Route exact path="/Print" element={<Print/>} /> 
              <Route exact path="/updateclass" element={<UpdateClass/>} /> 
              <Route exact path="/ResultUI" element={<ResultUI/>}/>
              <Route exact path="/ResultStudent" element={<ResultData/>}/>
              <Route exact path="/StudentSearch" element={<StudentSearch/>}/>
              <Route exact path="/StudentPaymentSearch" element={<StudentPaymentSearch/>}/>
              <Route exact path="/AdminResultShow" element={<AdminResultShow/>}/>


              
              
              
              
            </Routes>
          </Stack>

        </Box>
      </Router>
    </>
  )
}

export default Ecom












