import React, { useContext, useEffect, useState } from 'react'
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Tableval from './Tableval';
import StudentContexr from '../Context/StudentContexr';
const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));


const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));



const Classses = () => {
    const context = useContext(StudentContexr);
    const {setStudentUII,studentgrade} = context;

    const [rows, setRows] = useState([
        {
            "_id": "63c5a50e61b6e8c042fdbad1",
            "student_name": "aaa",
            "Address": "hdhdhd",
            "Phone": "jejej",
            "Total_payment": 5454,
            "date": "2023-01-16T19:27:10.502Z",
            "__v": 0
        },
        {
            "_id": "63c62b385d7437219f8e7f78",
            "student_name": "rj503@mail.com",
            "Address": "hamalrajesh",
            "Phone": "1234",
            "Total_payment": 5454,
            "date": "2023-01-17T04:59:36.872Z",
            "__v": 0
        },
        {
            "_id": "63c62b73678469de3149fe53",
            "student_name": "rj503@mail.com",
            "Address": "hamalrajesh",
            "Phone": "1234",
            "Total_payment": 5454,
            "date": "2023-01-17T05:00:35.573Z",
            "__v": 0
        },
        {
            "_id": "63c62b9ccb22edb6ed4c74c6",
            "student_name": "rj503@mail.com",
            "Address": "hamalrajesh",
            "Phone": "1234",
            "Total_payment": 5454,
            "date": "2023-01-17T05:01:16.539Z",
            "__v": 0
        }
    ]);
    const getstudents = async () => {
        // const response = await fetch(`http://localhost:3001/api/Students/fetchstudent`, {
            const response = await fetch(`https://naughty-pullover-fish.cyclic.app/api/Students/fetchstudent`, {
            method: 'GET',
    
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            }
        });
        const json = await response.json();
        console.log("the json is");
        // console.log(json);
        let result = json.filter((a)=>{
            return a.grade==studentgrade;
          });
          console.log(result)
        setRows(result);
    };
    useEffect(() => {
        getstudents();
      }, []);
    return (
        <>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 700 }} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>Student Name</StyledTableCell>
                            <StyledTableCell align="right">Address</StyledTableCell>
                            <StyledTableCell align="right">phone&nbsp;(g)</StyledTableCell>
                            <StyledTableCell align="right">Total Fee&nbsp;(g)</StyledTableCell>
                            
                            <StyledTableCell align="right">grade&nbsp;(g)</StyledTableCell>
                            <StyledTableCell align="right">Action&nbsp;(g)</StyledTableCell>
                            <StyledTableCell align="right"></StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            // <StyledTableRow key={row.student_name}>
                            //     <StyledTableCell component="th" scope="row">
                            //         {row.student_name}
                            //     </StyledTableCell>
                            //     <StyledTableCell align="right">{row.Address}</StyledTableCell>
                            //     <StyledTableCell align="right">{row.Phone}</StyledTableCell>
                            //     <StyledTableCell align="right">{row.Total_payment}</StyledTableCell>
                            //     <StyledTableCell align="right">{row.__v}</StyledTableCell>
                            // </StyledTableRow>
                            <Tableval key={row._id} row={row} />
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}

export default Classses
