import React, { useState } from 'react'

import { Box } from '@mui/material'


const StudentSearch = () => {
    const [abc, setAbc] = useState([
        {
            "subject1": {
                "name": "a",
                "marks": 0
            },
            "subject2": {
                "name": "d",
                "marks": 0
            },
            "subject3": {
                "name": "e",
                "marks": 0
            },
            "subject4": {
                "name": "t",
                "marks": 0
            },
            "subject5": {
                "name": "raaaaaaaaaaaaaaaaa",
                "marks": 0
            },
            "_id": "6456162936ba8f0a540b2177",
            "student_name": "lama",
            "grade": 9,
            "Phone": "9843393527",
            "date": "2023-05-06T08:56:09.809Z",
            "__v": 0
        }
    ]);
    const [error, setError] = useState(" ");
    const [qerror,setQerror] = useState("");
    const [open,setOpen] = useState(false);
    const getResult = async (e) => {

        e.preventDefault();

        const student_name = document.getElementById("SearchName").value;
        const grade = document.getElementById("SearchGrade").value;
        const Phone = document.getElementById("SearchPhone").value;
        const quarter = document.getElementById("SearchQuarter").value;

        if (Phone.length != 10) {
            setError("Phone Number length must be 10 ");

        } else {
            if (quarter>4||quarter<1) {
                setError(" ");
                setQerror("quarter must be 1 or 2 or 3 or4");
            } else {

                setError(" ");
                setQerror(" ");

                const data = { student_name, Phone, grade, quarter };
                const options = {
                    method: "PUT",
                    body: JSON.stringify(data),
                    headers: { "Content-Type": "application/json" },
                };

                try {
                    // const response = await fetch("http://localhost:3001/api/result/particularStudent", options);
                    const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/result/particularStudent", options);
                    const json = await response.json();
                    console.log(json);
                    if (json == false) {

                        window.alert("No Result exist of such details");
                        setOpen(false);

                    }
                    else {

                        setAbc(json);
                        setOpen(true);
                    }
                } catch (error) {
                    console.error(error);

                }
            }
        }
    }

    return (
        <Box flex={4} sx={{ display: "flex", alignItems: "flex-start", justifyContent: "space-evenly", paddingTop: 4 }} >
            <form className='spsf' onSubmit={getResult}>
                <input required className='inputcss' type='text' placeholder='Name' id="SearchName" />
                <input required className='inputcss' type='number' placeholder='Grade' id="SearchGrade" />
                <input required className='inputcss' type='number' placeholder='Phone No.' id="SearchPhone" />{error}
                <input required className='inputcss' type='number' placeholder='Quarter' id="SearchQuarter" />{qerror}
                {/* <button className='buttoncss' onClick={getResult}>Result</button> */}
                <input type='submit'  className='buttoncss'/> 
            </form>

            {open?<div>

                <div>
                    <h1>Student Result</h1>
                    <p>Name: {abc[0].student_name}</p>
                    <p>Class: {abc[0].grade}</p>
                    <p>Phone: {abc[0].Phone}</p>
                    <p>quarter:{abc[0].quarter}</p>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th className='thc'>Subject</th>
                            <th className='thc'>Maximum Marks</th>
                            <th className='thc'>Pass marks</th>
                            <th className='thc'>Marks Obtained</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td className='tdc'>{abc[0].subject1.name}</td>
                            <td className='tdc'>100</td>
                            <td className='tdc'>40</td>
                            <td className='tdc'>{abc[0].subject1.marks}</td>
                        </tr>
                        <tr>
                            <td className='tdc'>{abc[0].subject2.name}</td>
                            <td className='tdc'>100</td>
                            <td className='tdc'>40</td>
                            <td className='tdc'>{abc[0].subject2.marks}</td>
                        </tr>
                        <tr>
                            <td className='tdc'>{abc[0].subject3.name}</td>
                            <td className='tdc'>100</td>
                            <td className='tdc'>40</td>
                            <td className='tdc'>{abc[0].subject3.marks}</td>
                        </tr>
                        <tr>
                            <td className='tdc'>{abc[0].subject4.name}</td>
                            <td className='tdc'>100</td>
                            <td className='tdc'>40</td>
                            <td className='tdc'>{abc[0].subject4.marks}</td>
                        </tr>
                        <tr>
                            <td className='tdc'>{abc[0].subject5.name}</td>
                            <td className='tdc'>100</td>
                            <td className='tdc'>40</td>
                            <td className='tdc'>{abc[0].subject5.marks}</td>
                        </tr>
                    </tbody>
                </table>
                <div>
                    <p>Total Marks Obtained: {abc[0].subject1.marks + abc[0].subject2.marks + abc[0].subject3.marks + abc[0].subject4.marks + abc[0].subject5.marks}/500</p>
                    Remarks:{abc[0].subject1.marks > 31 && abc[0].subject2.marks > 31 && abc[0].subject3.marks > 31 && abc[0].subject4.marks > 31 && abc[0].subject5.marks > 31 ? "Congratulation,You passed the exam" : "Failed,Increase Practice Time"}
                </div>

            </div>:<div></div>}
        </Box>
    )
}

export default StudentSearch