import React, { useState } from 'react'
import StudentContexr from './StudentContexr'

const StudentState = (props) => {
    const [studentUII, setStudentUII] = useState("");
    const [studentgrade, setStudentgrade] = useState("");
    const [studenttotal, setStudenttotal] = useState("");
    
    const [studentphone, setStudentphone] = useState("");
    
    const [studentQuarter, setStudentquarter] = useState("");
    const [studentYear,setStudentYear] = useState("");
    const [resultGrade,setResultGrade] = useState("");
  return (
    <StudentContexr.Provider value={{studentUII,setStudentUII,studentgrade,setStudentgrade,studenttotal, setStudenttotal,setStudentphone,studentphone,studentQuarter,setStudentquarter,studentYear,setStudentYear,resultGrade,setResultGrade}}> 
        {/* it can also be value= {{state:state,update:update}} */}
            {props.children}
        </StudentContexr.Provider>
  )
}

export default StudentState
