import { AppBar, Box, Button, IconButton, styled, Toolbar, Typography } from '@mui/material'
import MenuIcon from '@mui/icons-material/Menu';
import React, { useState } from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  Navigate
} from "react-router-dom";
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition'
import MicIcon from '@mui/icons-material/Mic';
import { useNavigate } from 'react-router-dom';

const Navbar = (props) => {

  let history = useNavigate();
  let { sidenav, setSidenav, setADrawer } = props;
  let localstrg = localStorage.getItem('token');
  const StyledToolbar = styled(Toolbar)({
    display: "flex",
    justifyContent: "space-between"
  });
  const handleSidenavclick = () => {
    console.log(sidenav);
    setSidenav(!sidenav);
    console.log(sidenav);

  };
  const handlesetADrawer = () => {
    setADrawer(true);
  }

  const logout = (e) => {
    // e.ppppa
    localStorage.removeItem('token');
    history("/login");
    setSidenav(!sidenav);

  }
  // for the speech

  // navigator.mediaDevices.getUserMedia({ audio: true });
  const [redirectUrl, setRedirectUrl] = useState("");
  const pages = ["home", "classes", "student", "add student"];
  const urls = {
    home: "/",
    classes: "/about",
    student: "/student",
    "add student": "/Addstudent"
  };
  const commands = [
    {
      command: ["Go To *", "Open *"],
      callback: (redirectPage) => setRedirectUrl(redirectPage)
    }
  ];
  let { transcript } = useSpeechRecognition({ commands });
  const { listening } = useSpeechRecognition();

  let redirects = "";
  if (redirectUrl) {
    if (pages.includes(redirectUrl)) {
      redirects = <Navigate to={urls[redirectUrl]} />
    } else {
      redirects = <p>Could not find the page:{redirectUrl}</p>
    }

  }


  return (
    <AppBar position="static" sx={{ mb: .5, backgroundColor: "rgb(218, 221, 223)", color: "#151515" }}>
      <StyledToolbar variant="dense">
        <Box sx={{ display: "flex", flexDirection: "row" }}>
          <IconButton sx={{ display: { xs: "none", sm: "block" } }} edge="start" color="inherit" aria-label="menu" onClick={handleSidenavclick}>
            <MenuIcon />
          </IconButton>
          <IconButton sx={{ display: { xs: "block", sm: "none" } }} edge="start" color="inherit" aria-label="menu" onClick={handlesetADrawer}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h5" color="inherit" component="div">
            Nepal School
          </Typography>
        </Box>

        {/* <button onClick={SpeechRecognition.startListening}>Start</button> */}
        {/* <button onClick={SpeechRecognition.stopListening}>Stop speech</button> */}

        <Box sx={{ display: "flex", flexDirection: "column" }}>
          <IconButton aria-label="fingerprint" color="secondary" onClick={SpeechRecognition.startListening}>
            <MicIcon />
          </IconButton>
          {listening ? 'Listening..' : ''}
        </Box>
        transcript:{transcript}
        {redirects}
        {localstrg ?
          <Box>
            <IconButton edge="start" color="inherit" aria-label="menu" onClick={logout}>
              <Button variant="contained" color="primary">Logout</Button>
            </IconButton>
          </Box> :
          <Box>
            <IconButton component={Link} to="/login" edge="start" color="inherit" aria-label="menu">
              <Button variant="contained" color="primary">Log in</Button>
            </IconButton>
          </Box>}
      </StyledToolbar>
    </AppBar>
  )
}

export default Navbar
