import React, { useContext, useState } from 'react'
import { styled } from '@mui/material/styles';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import {
    BrowserRouter as Router,
    Link
} from "react-router-dom";
import StudentContexr from '../Context/StudentContexr';
import { Button } from '@mui/material';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));


const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));
const Studentval = (props) => {

    const context = useContext(StudentContexr);
    const { setStudentUII, studentgrade, setStudenttotal, setStudentgrade, setStudentphone } = context;
    let { row } = props;
    const [data, setData] = useState(row);
    const Clickfunction = () => {
        console.log("i am clicked");
        setStudentUII(data.student_name);
        setStudenttotal(data.Total_payment);
        setStudentgrade(data.grade);
    }
    const paymentfunction = () => {
        console.log("clicked clicked");
        setStudentUII(data.student_name);
        setStudentgrade(data.grade);
        setStudentphone(data.Phone);
    }



    const deleteStudent = async () => {
        let student_name = row.student_name;
        let grade = row.grade;
        const data = { student_name, grade };
        let a = window.confirm("Are you sure you want to delete");
        if (a==true) {
            const options = {
                method: "DELETE",
                body: JSON.stringify(data),
                headers: { "Content-Type": "application/json" },
            };
            try {
                // const response = await fetch("http://localhost:3001/api/Students/deletestudent", options);
                const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Students/deletestudent", options);
                const json = await response.json();
                console.log(json);

            } catch (error) {
                console.error(error);
            }
            window.location.reload(false);
        } 
        else {

        }



    }
    console.log(data);
    return (
        <StyledTableRow key={row.student_name}>
            <StyledTableCell component="th" scope="row">
                {row.student_name}
            </StyledTableCell>
            <StyledTableCell align="right">{row.Address}</StyledTableCell>
            <StyledTableCell align="right">{row.Phone}</StyledTableCell>
            <StyledTableCell align="right">{row.Total_payment}</StyledTableCell>
            <StyledTableCell align="right">
                {row.grade}
            </StyledTableCell>
            <StyledTableCell align="right">
                {/* <Link  aria-current="page" to="/student" onClick={()=>setStudentUII(data.student_name)}> */}
                <Link aria-current="page" to="/student" onClick={Clickfunction}>
                    <Button variant="contained">DATA</Button>
                </Link>
            </StyledTableCell>
            {/* for adding the payment */}
            <StyledTableCell align="right">
                {/* <Link  aria-current="page" to="/student" onClick={()=>setStudentUII(data.student_name)}> */}
                <Link aria-current="page" to="/AddPayment" onClick={paymentfunction}>
                    <Button variant="contained">Add Pay</Button>
                </Link>
            </StyledTableCell>
            <StyledTableCell align="right">
                <Button variant="contained" onClick={deleteStudent}>Delete</Button>
            </StyledTableCell>
        </StyledTableRow>
    )
}

export default Studentval
