import React from 'react'
import { Box, Button, Card, IconButton, Input, TextField } from '@mui/material'
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';


const AddAdmin = () => {

  let history = useNavigate();
  useEffect(() => {
    if (localStorage.getItem('token')) {
      console.log("toiken available")
    }
    else {
      history("/login");
    }
    // eslint-disable-next-line
  }, []);
  const [phoneHelperText, setPhoneHelperText] = useState("Please enter your Phone Number");
 
  const handleSubmit = async (e) => {
    console.log("clickd")
    e.preventDefault();
    const Admin_name = document.getElementById("NewAdminName").value;
    const Phone = document.getElementById("NewAdminphone").value;
    const Admin_post = document.getElementById("NewAdminpost").value;
    const Password = document.getElementById("NewAdminpassword").value;

    const data = { Admin_name, Phone, Admin_post, Password };
    const options = {
      method: "POST",
      body: JSON.stringify(data),
      headers: { "Content-Type": "application/json" },
    };
    if (Phone.length != 10) {
      setPhoneHelperText("Phone no must be 10 digit");
      window.alert("The phone no must be 10 digit");

    } else {
      
        
        setPhoneHelperText("Filled")
        try {
          // const response = await fetch("http://localhost:3001/api/Admin/Signup", options);
          const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Admin/Signup", options);
          const json = await response.json();
          console.log(json);
          if (json.success) {
            window.alert("Admin is added");
          }
          if (!json.success) { window.alert("Wrong Information"); }
        } catch (error) {
          console.error(error);
        }
        
      
      
    }

  };
  return (
    <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center" }} >
     <form onSubmit={handleSubmit} style={{"width":"100%","margin":"50px","display":"flex","align-items":"center","justify-content":"center"}}>
      <Card variant="outlined" sx={{ width: "50%", borderRadius: "16px" }}>
        <Box sx={{ backgroundColor: "skyblue", color: "white" }}> <h2>&nbsp;Add Admin Form</h2></Box>
        <TextField required helperText="Please enter new Admin name" fullWidth label="New Admin Name" variant="filled" id="NewAdminName" margin="dense" />
        <TextField required helperText={phoneHelperText} fullWidth label="Phone number" variant="filled" id="NewAdminphone" margin="dense" />
        <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
          <TextField required sx={{ width: "48%" }} helperText="Please enter Admin Post" label="Admin's Post" id="NewAdminpost" variant="filled" margin="dense" />
          <TextField inputProps={{ minLength: 6}} required sx={{ width: "48%" }} helperText="Password must be of min length 6" label="Password" id="NewAdminpassword" type="password" variant="filled" margin="dense" />

        </Box>

        <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
          <Button sx={{ width: "90%", margin: "20px" }} variant="contained" type='submit'><h3>Submit</h3></Button>
        </Box>
      </Card>
      </form>

    </Box>
  )
}

export default AddAdmin
