import React, { useState } from 'react'

import { Box } from '@mui/material'


const StudentPaymentSearch = () => {
    const [studentPaySearch, setStudentPaySearch] = useState([
        {
            "_id": "643ecdb743a0daf3451ffe36",
            "student_name": "Unknown",
            "year": "0",
            "grade": "0",
            "Phone": "98------",
            "Total_payment_done": 1,
            "date": "2023-04-18T17:04:55.105Z",
            "__v": 0
        }
    ])


    const [gradeError, setGradeError] = useState(" ");
    const [phoneError, setPhoneError] = useState(" ");
    const [open,setOpen] = useState(false);
    const getPayment = async () => {
        const student_name = document.getElementById("PaymentName").value;
        const grade = document.getElementById("PaymentGrade").value;
        const Phone = document.getElementById("PaymentPhone").value;
        const data = { student_name, Phone, grade };
        const options = {
            method: "PUT",
            body: JSON.stringify(data),
            headers: { "Content-Type": "application/json" },
        };

        if (grade < 5 || grade > 10) {
            setGradeError("Grade should be more than 4 and less than 11");

        } else {
            if (Phone.length != 10) {
                setPhoneError("the phone should be of length 10");
                setGradeError(" ");
            } else {
                setPhoneError(" ");
                try {
                    // const response = await fetch("http://localhost:3001/api/Studentpayment/paymentfetch", options);
                    const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Studentpayment/paymentfetch", options);
                    const json = await response.json();
                    // console.log(json);
                    if (json == false) {
                        window.alert("no payment exist of given information");
                        setOpen(false);
                    }
                    else {
                        setStudentPaySearch(json);
                        setOpen(true)
                    }
                    
                } catch (error) {
                    console.error(error);
                }
            }


        }


    }

    return (
        <Box flex={4} sx={{ display: "flex", alignItems: "flex-start", justifyContent: "space-evenly", paddingTop: 4 }} >
            <div className='spsf'>
                <input className='inputcss' type='text' placeholder='Name' id="PaymentName" />
                <input className='inputcss' type='number' placeholder='Grade(eg: 6)' id="PaymentGrade" />{gradeError}
                <input className='inputcss' type='number' placeholder='Phone No.' id="PaymentPhone" />{phoneError}
                <button className='buttoncss' onClick={getPayment}>Submit</button>
            </div>
            {open?
            <div>
                <table>
                    <thead>
                        <tr>
                            <th className='thc'>Student Name</th>
                            <th className='thc'>Year</th>
                            <th className='thc'>Phone</th>
                            <th className='thc'>Paid</th>
                            <th className='thc'>Grade</th>
                        </tr>
                    </thead>
                    <tbody>
                        {studentPaySearch.map((row) => (
                            <tr key={row._id}>
                                <td className='tdc'>
                                    {row.student_name}
                                </td>
                                <td className='tdc'>{row.year}</td>
                                <td className='tdc'>{row.Phone}</td>
                                <td className='tdc'>{row.Total_payment_done}</td>
                                <td className='tdc'>{row.grade}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>:<div></div>}
        </Box>
    )
}

export default StudentPaymentSearch