import { Box, List, ListItemButton, ListItemIcon, ListItemText, ListSubheader } from '@mui/material'
import SendIcon from '@mui/icons-material/Send';
import ChangeCircleIcon from '@mui/icons-material/ChangeCircle';
import DashboardIcon from '@mui/icons-material/Dashboard';
import ClassIcon from '@mui/icons-material/Class';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import React, { useEffect, useState, useContext } from 'react'

import {
  Link,
  useNavigate
} from "react-router-dom";
import StudentContexr from '../Context/StudentContexr';
const Sidebar = (props) => {
  const context = useContext(StudentContexr);
  const { setStudentYear, setStudentquarter,setResultGrade } = context;

  const history = useNavigate();

  let { sidenav } = props;
  const [open, setOpen] = useState(true);
  
  const handleClick = () => {
    setOpen(!open);
    var values = prompt("Enter year,grade,quarter eg 2023,6,1").split(",");

    if (values === null) {
      // The user clicked cancel, so do nothing or return
      return;
    } else {
      setStudentYear(values[0]);
      setResultGrade(values[1]);
      setStudentquarter(values[2]);
      history("/AdminResultShow");
    }

  };
  // const [localstrg, setLocalstrg] = useState(localStorage.getItem('token'));
  const localstrg = localStorage.getItem('token');

  return (
    <>
      {localstrg ?
        <Box flex={sidenav ? 1 : 0.2} sx={{ display: { xs: "none", sm: "block" }, backgroundColor: sidenav ? "black" : "grey.800", color: "grey.400" }}>
          <List
            sx={{ width: '100%', maxWidth: 360 }}
            component="nav"
            aria-labelledby="nested-list-subheader"
            subheader={
              <ListSubheader component="div" id="nested-list-subheader" sx={{ backgroundColor: sidenav ? "black" : "grey.800", color: "grey.400" }}>
                Header
              </ListSubheader>
            }
          >
            <ListItemButton component={Link} to={'/'}>
              <ListItemIcon>
                <DashboardIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Dashboard" : ""} />
            </ListItemButton>

            <ListItemButton component={Link} to={'/Addstudent'}>
              <ListItemIcon>
                <SendIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Add student" : ""} />
            </ListItemButton>

            <ListItemButton component={Link} to={'/classlist'}>
              <ListItemIcon>
                <ClassIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Classes" : ""} />
            </ListItemButton>

            <ListItemButton component={Link} to={'/AddAdmin'}>
              <ListItemIcon>
                <SendIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Add Admin" : ""} />
            </ListItemButton>


            <ListItemButton component={Link} to={'/Passwordchange'}>
              <ListItemIcon>
                <ChangeCircleIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Change password" : ""} />
            </ListItemButton>

            <ListItemButton component={Link} to={'/updateclass'}>
              <ListItemIcon>
                <ChangeCircleIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Update Class" : ""} />
            </ListItemButton>

            <ListItemButton onClick={handleClick}>
              <ListItemIcon>
                <InboxIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Result Analysis" : ""} />
            </ListItemButton>

            <ListItemButton component={Link} to={'/ResultUI'}>
              <ListItemIcon>
                <InboxIcon sx={{ color: "white" }} />
              </ListItemIcon>
              <ListItemText primary={sidenav ? "Add Result" : ""} />
            </ListItemButton>

          </List>
        </Box> : <Box></Box>
      }
    </>
  )
}

export default Sidebar
