import { Box, Button, Card, IconButton, Snackbar, TextField } from '@mui/material';
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import CloseIcon from '@mui/icons-material/Close';

const Login = () => {
  const [open, setOpen] = useState(false);

  
  const handleClose = () => {
    setOpen(false);
  };

  const action = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );

  let history = useNavigate();
  const onSubmitdata = async (e) => {
    e.preventDefault();
    const name = document.getElementById("name").value;
    const password = document.getElementById("password").value;
    // const response = await fetch("http://localhost:3001/api/Admin/Login", {
      const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/Admin/Login", {
      method: 'PUT',

      headers: {
        'Content-Type': 'application/json',
        // 'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({ Admin_name: name, Password: password }) // body data type must match "Content-Type" header
    });
    const json = await response.json();
    console.log(json);
    if (json.success) {
      localStorage.setItem('token', json.token);
      history("/");
    }
    if(!json.success){
      setOpen(true);
      console.log("wrong credentials")
    }



  }

  return (
    

      <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center" }} >
        <form onSubmit={onSubmitdata} style={{margin:"100px"}}>
          <Card variant="outlined" sx={{ width: "100%", borderRadius: "16px" }}>
            <Box sx={{ backgroundColor: "skyblue", color: "white" }}> <h2>&nbsp;Admin login</h2></Box>
            <TextField required helperText="Please enter your name" fullWidth label="Admin Name" variant="filled" id="name" margin="dense" />
            <TextField required type="Password" helperText="Please enter your password" fullWidth label="Password" variant="filled" id="password" margin="dense" />

            <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
              <Button sx={{ width: "90%", margin: "20px" }} variant="contained" type="submit"><h3>Submit</h3></Button>
            </Box>
          </Card>
        </form>
        <div className='btrt'>
          <div>
            <h3>Records for the Students</h3>
          </div>
          <Link aria-current="page" to="/StudentPaymentSearch" >
                <Button sx={{mr:2}} variant="contained">Payment </Button>
              </Link>
              <Link aria-current="page" to="/StudentSearch" >
                <Button sx={{mr:1}} variant="contained">Result</Button>
              </Link>
        </div>
        <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message="wrong credentials"
        action={action}
      />
      </Box>

  )
}

export default Login
