import React, { useContext, useEffect, useState } from 'react'
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Tableval from './Tableval';
import StudentContexr from '../Context/StudentContexr';
const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

const Studentui = () => {
  const context = useContext(StudentContexr);
  const {studentgrade, studentUII, studenttotal } = context;
  const [studentdata, setStudentdata] = useState([]);

  const [paymentRemain, setPaymentRemain] = useState(0);
  const getStudent = async () => {
    let student_name = studentUII;
    let grade = studentgrade;
    let bbody = JSON.stringify({ student_name });
    // const response = await fetch(`http://localhost:3001/api/Studentpayment/studentfetch`, {
      const response = await fetch(`https://naughty-pullover-fish.cyclic.app/api/Studentpayment/studentfetch`, {
      method: 'PUT',

      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({ student_name ,grade})
    });
    let a = await response.json();
    console.log("the value of a is")
    console.log(a);
    setStudentdata(a);
    let b = 0;
    a.map((row) => {
      b = b + row.Total_payment_done;
    });
    console.log(b);
    setPaymentRemain(studenttotal-b);
    console.log(paymentRemain);

  };
  useEffect(() => {
    getStudent();
  }, []);

  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Total = {studenttotal}</StyledTableCell>
              <StyledTableCell>Remaining = {paymentRemain}</StyledTableCell>

            </TableRow>
            <TableRow>
              <StyledTableCell>Srudent Name</StyledTableCell>
              <StyledTableCell align="right">Year</StyledTableCell>
              <StyledTableCell align="right">phone</StyledTableCell>
              <StyledTableCell align="right">Paid</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {studentdata.map((row) => (
              <StyledTableRow key={row._id}>
                <StyledTableCell component="th" scope="row">
                  {row.student_name}
                </StyledTableCell>
                <StyledTableCell align="right">{row.year}</StyledTableCell>
                <StyledTableCell align="right">{row.Phone}</StyledTableCell>
                <StyledTableCell align="right">{row.Total_payment_done}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  )
}

export default Studentui
