import { Box, Paper } from '@mui/material'
import React from 'react'
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';

import Button from '@mui/material/Button';
import StudentContexr from '../Context/StudentContexr';
import {useContext, useState } from 'react';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Studentval from './Studentval';
const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));


const Dashboard = () => {
    const context = useContext(StudentContexr);
    const { studentUII, studenttotal,setStudentUII } = context;

    const [studentdata, setstudentdata] = useState([{"abc":"efg"}]);
    const [store_stdata,setstore_stdata] = useState([{"nothing":"something"}]);
    // for the search student
    const [searchTerm, setSearchTerm] = useState({ name: '', grade: '' });
    const [searchResults, setSearchResults] = useState([]);


    const [sorted, setSorted] = useState(['apple', 'ball', 'abk']);

    const handleInputChange = (event) => {
        const sname = document.getElementById("studentName").value;
        const sgrade = document.getElementById("studentGrade").value;

        const thedata = [...store_stdata];
        const filteredStudents = thedata.filter(student =>
            student.student_name.toLowerCase().includes(sname.toLowerCase()) && student.grade.toString().includes(sgrade)
        );

        setstudentdata(filteredStudents);

        // here the sorted thing    
        let thesorted = studentdata.sort(function(a, b) {
            if (a.student_name < b.student_name) {
                return -1;
            }
            if (a.student_name > b.student_name) {
                return 1;
            }
            return 0;
        });
        console.log("the sorted value is ");
        console.log(thesorted);
        console.log(store_stdata);



        
        
    };

    const handleBinarySearch = () => {
        const sname = document.getElementById("studentName").value;
        const sgrade = document.getElementById("studentGrade").value;
       
        // setstudentdata(store_stdata);
        let thesorted = store_stdata.sort(function(a, b) {
            if (a.student_name < b.student_name) {
                return -1;
            }
            if (a.student_name > b.student_name) {
                return 1;
            }
            return 0;
        });
        const value = [...thesorted];

        let arraySize = value.length;
        let lowerbound = 0;
        let upperbound = arraySize-1;
        let notfound = true;
        
        while (notfound) {
            if (upperbound<lowerbound) {
                notfound = false;
                
                continue;        
            }
            let midpoint = lowerbound + Math.floor((upperbound - lowerbound)/2);
            if (value[midpoint].student_name.toLowerCase()<sname.toLowerCase()) {
                lowerbound = midpoint + 1;
                
            }

            if (value[midpoint].student_name.toLowerCase()>sname.toLowerCase()) {
                upperbound = midpoint - 1;
                
                continue;
            }
            if (value[midpoint].student_name.toLowerCase() == sname.toLowerCase()) {
                notfound = false;
                setstudentdata([value[midpoint]]);
                
                continue;
            
            }
            
            
        }
        
        
    };









    let totalstudent;
    const totalpayment = 10000;
    let history = useNavigate();
    const getStudent = async () => {
        // const response = await fetch(`http://localhost:3001/api/Students/fetchstudent`, {
            const response = await fetch(`https://naughty-pullover-fish.cyclic.app/api/Students/fetchstudent`, {
            method: 'GET',

            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            },
        });
        let a = await response.json();
        console.log("the value of a is")
        console.log(a);
        setstudentdata(a);
        setstore_stdata(a);
        totalstudent = studentdata.length;

    };
    useEffect(() => {
        getStudent();
        if (localStorage.getItem('token')) {
            console.log("toiken available")
        }
        else {
            history("/login");
        }
    }, []);
    return (
        <Box flex={4} sx={{
            display: 'flex',
            flexWrap: 'wrap',
            flexDirection: "row",
            height: "95vh"
        }}>
            <h1>Dasboard</h1>
            <Box sx={{
                display: 'flex',
                flexWrap: 'wrap',
                flexDirection: "column",
                m: 1,
                width: "95%",
                height: "30%",
            }}>
                <Paper elevation={3} sx={{
                    padding: "10px",
                    backgroundColor: "grey.800",
                    width: "30%",
                    color: "rgb(185, 226, 212)",
                    height: "100%",
                    marginRight: "10px"
                }} >
                    
                    <h1>Total student:{studentdata.length}</h1></Paper>



                {/* it is for searching the student */}


                <Paper elevation={3} sx={{ backgroundColor: "white", color: "rgb(185, 226, 212)", padding: "10px", width: "65%", height: "100%" }} >
                    <div>
                        
                        <TextField id="studentName" label="Student Name" variant="filled" sx={{ m: 2 }} />
                       
                        <TextField id="studentGrade" label="Student Grade" variant="filled" sx={{ m: 2 }} />
                        <Button variant="contained" onClick={handleInputChange} sx={{ m: 2 }}>Search</Button>
                        <Button variant="contained" onClick={handleBinarySearch} sx={{ m: 2 }}>Exact</Button>
                        
                    </div>
                </Paper>
            </Box>
            <Box sx={{
                m: 1,
                width: "95%",
                height: "55%",
            }}>
                <Paper elevation={3} sx={{ width: "100%", height: "100%" }} >
                    <TableContainer component={Paper}>
                        <Table sx={{ minWidth: 700 }} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell>Student Name</StyledTableCell>
                                    <StyledTableCell align="right">Address</StyledTableCell>
                                    <StyledTableCell align="right">Phone</StyledTableCell>
                                    <StyledTableCell align="right">Total payment</StyledTableCell>                                    
                                    <StyledTableCell align="right">Grade</StyledTableCell>
                                    <StyledTableCell align="right">Record</StyledTableCell>
                                    <StyledTableCell align="right">Action</StyledTableCell>
                                    <StyledTableCell align="right"></StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {studentdata.slice(0,5).map((row) => (
                                    // <StyledTableRow key={row._id}>
                                    //     <StyledTableCell component="th" scope="row">
                                    //         {row.student_name}
                                    //     </StyledTableCell>
                                    //     <StyledTableCell align="right">{row.grade}</StyledTableCell>
                                        
                                    // </StyledTableRow>
                                    <Studentval key={row.student_name} row={row} />
                                    
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>

                </Paper>
            </Box>

        </Box>
    )
}

export default Dashboard
