import React from 'react'
import { Box } from '@mui/material'
import { useEffect, useState } from 'react';
import { useContext } from 'react'
import StudentContexr from '../../Context/StudentContexr';

const ResultData = () => {
    const context = useContext(StudentContexr);
    const { studentUII, studentgrade, studentphone, studentQuarter } = context;
    const [abc, setAbc] = useState([
        {
            "subject1": {
                "name": "a",
                "marks": 0
            },
            "subject2": {
                "name": "d",
                "marks": 0
            },
            "subject3": {
                "name": "e",
                "marks": 0
            },
            "subject4": {
                "name": "t",
                "marks": 0
            },
            "subject5": {
                "name": "raaaaaaaaaaaaaaaaa",
                "marks": 0
            },
            "_id": "6456162936ba8f0a540b2177",
            "student_name": "lama",
            "grade": 9,
            "Phone": "9843393527",
            "date": "2023-05-06T08:56:09.809Z",
            "__v": 0
        }
    ]);
    const [ratio, setRatio] = useState([1.2, 2.3, 1.2]);
    const [open , setOpen] = useState(false);

    const getResult = async () => {
        const student_name = studentUII;
        const grade = studentgrade;
        const Phone = studentphone;
        const quarter = studentQuarter;
        const data = { student_name, Phone, grade, quarter };
        const options = {
            method: "PUT",
            body: JSON.stringify(data),
            headers: { "Content-Type": "application/json" },
        };
        console.log("yhe state are ");
        console.log(quarter, student_name, grade, Phone,);

        try {
            // const response = await fetch("http://localhost:3001/api/result/particularStudent", options);
            const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/result/particularStudent", options);
            const json = await response.json();
            // console.log(json);
            if (json == false) {
                window.alert("No Result exist of " + quarter + "quarter");

            } else {

                setAbc(json);
                setOpen(true);
                
            }
        } catch (error) {
            console.error(error);
        }
        try {
            // const response = await fetch("http://localhost:3001/api/result/allStudent");
            const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/result/allStudent");
            const json = await response.json();
            let ab = json.filter(item => {
                return (item.student_name === student_name && item.grade == grade);
            });


            let e = [];
            for (let i = 0; i < ab.length; i++) {
                let total = ab[i].subject1.marks + ab[i].subject2.marks + ab[i].subject3.marks + ab[i].subject4.marks + ab[i].subject5.marks;
                e.push(total);

            }
            let Ratio = [];
            for (let i = 0; i < e.length - 1; i++) {
                let d = e[i + 1] / e[i];
                Ratio.push(d);

            }

            setRatio(Ratio);
            console.log("the Ratio is:");
            console.log(Ratio);

        } catch (error) {
            console.error(error);
        }
    }
    useEffect(() => {
        getResult();
    }, []);



    return (
        <>
        {open?<Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "space-around" }} >
            <div>
                <h1>Student Result</h1>
                <p>Name: {abc[0].student_name}</p>
                <p>Class: {abc[0].grade}</p>
                <p>Phone: {abc[0].Phone}</p>
                <p>quarter:{abc[0].quarter}</p>
            </div>
            <table>
                <thead>
                    <tr>
                        <th className='thc'>Subject</th>
                        <th className='thc'>Maximum Marks</th>
                        <th className='thc'>Pass marks</th>
                        <th className='thc'>Marks Obtained</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className='tdc'>{abc[0].subject1.name}</td>
                        <td className='tdc'>100</td>
                        <td className='tdc'>40</td>
                        <td className='tdc'>{abc[0].subject1.marks}</td>
                    </tr>
                    <tr>
                        <td className='tdc'>{abc[0].subject2.name}</td>
                        <td className='tdc'>100</td>
                        <td className='tdc'>40</td>
                        <td className='tdc'>{abc[0].subject2.marks}</td>
                    </tr>
                    <tr>
                        <td className='tdc'>{abc[0].subject3.name}</td>
                        <td className='tdc'>100</td>
                        <td className='tdc'>40</td>
                        <td className='tdc'>{abc[0].subject3.marks}</td>
                    </tr>
                    <tr>
                        <td className='tdc'>{abc[0].subject4.name}</td>
                        <td className='tdc'>100</td>
                        <td className='tdc'>40</td>
                        <td className='tdc'>{abc[0].subject4.marks}</td>
                    </tr>
                    <tr>
                        <td className='tdc'>{abc[0].subject5.name}</td>
                        <td className='tdc'>100</td>
                        <td className='tdc'>40</td>
                        <td className='tdc'>{abc[0].subject5.marks}</td>
                    </tr>
                </tbody>
            </table>
            <div>
                <p>Total Marks Obtained: {abc[0].subject1.marks + abc[0].subject2.marks + abc[0].subject3.marks + abc[0].subject4.marks + abc[0].subject5.marks}/500</p>
                <h3>Result:{abc[0].subject1.marks > 31 && abc[0].subject2.marks > 31 && abc[0].subject3.marks > 31 && abc[0].subject4.marks > 31 && abc[0].subject5.marks > 31 ? "Congratulation,You passed the exam" : "Failed,Increase Practice Time"}
                </h3>
                {ratio.map((item, index) => (
                    <p key={index}>
                        Your progress from term {index + 1} to term {index + 2} is {item.toFixed(2)} times.
                    </p>
                ))}
            </div>

        </Box>:<Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "space-around" }} >No Result exist</Box>}
        </>
    )
}

export default ResultData