import React from 'react'
import { Box } from '@mui/material'
import { useState } from 'react'

const ResultUI = () => {
  const [gradeError, setGradeError] = useState(" ");
  const [phoneError, setPhoneError] = useState(" ");
  const [quarterError, setQuarterError] = useState(" ");


  const resultpost = async (e) => {
    e.preventDefault();
    console.log("clicked");
    const name = document.getElementById("RName").value;
    const grade = document.getElementById("RGrade").value;
    const phone = document.getElementById("RPhone").value;
    const address = document.getElementById("address").value;
    const date = document.getElementById("date").value;
    const quarter = document.getElementById("quarter").value;

    // const subject1 = document.getElementById("subject1name").value;
    const subject1m = document.getElementById("subject1marks").value;
    const subject1data = {
      "name": "Math",
      "marks": subject1m
    }

    // const subject2 = document.getElementById("subject2name").value;
    const subject2m = document.getElementById("subject2marks").value;
    const subject2data = {
      "name": "English",
      "marks": subject2m
    }

    // const subject3 = document.getElementById("subject3name").value;
    const subject3m = document.getElementById("subject3marks").value;
    const subject3data = {
      "name": "Science",
      "marks": subject3m
    }

    // const subject4 = document.getElementById("subject4name").value;
    const subject4m = document.getElementById("subject4marks").value;
    const subject4data = {
      "name": "Nepali",
      "marks": subject4m
    }

    // const subject5 = document.getElementById("subject5name").value;
    const subject5m = document.getElementById("subject5marks").value;
    const subject5data = {
      "name": "Grammer",
      "marks": subject5m
    }

    if (phone.length != 10) {
      setPhoneError("Phone No must be of 10 digit")

    } else {
      if (grade < 5 || grade > 10) {
        setGradeError("grade must between 5-10");
        setPhoneError("");

      } else {
        if (quarter < 1 || quarter > 4) {
          setQuarterError(" quarter must be between 1-4");
          setGradeError("");
          setPhoneError("");

        } else {
          setGradeError("");
          setPhoneError("");
          setQuarterError("");
          // const response = await fetch("http://localhost:3001/api/result/studentResult", {
            const response = await fetch("https://naughty-pullover-fish.cyclic.app/api/result/studentResult", {
            method: 'POST',

            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Origin': '*'
            },
            body: JSON.stringify({ student_name: name, Address: address, year: date, Phone: phone, quarter: quarter, grade: grade, subject1: subject1data, subject2: subject2data, subject3: subject3data, subject4: subject4data, subject5: subject5data })
          });

          const json = await response.json();
          console.log(json);
          if (json == "success") {
            window.alert("added result");
            // history("/about");
            //   give the alert
          }
          if (json != "success") {

            window.alert("something went wrong")
          }

        }

      }

    }
  }



  return (
    <Box flex={4} sx={{ display: "flex", alignItems: "center", justifyContent: "center", flexDirection: 'row' }} >
      <form onSubmit={resultpost} style={{ "width": "100%", "margin": "50px", "display": "flex", "align-items": "center", "justify-content": "center" }}>

        <div className='mar'>
          <div>Name:<input required className='buttoncss2' type='text' placeholder='Name' id="RName" /></div>

          <div>Grade: <input required className='buttoncss2' type='text' placeholder='Grade' id="RGrade" /> </div>
          { gradeError}

          <div>Phone: <input required className='buttoncss2' type='number' placeholder='Phone' id="RPhone" /></div>
          {phoneError}

          <div>Address: <input required className='buttoncss2' type='text' placeholder='Address' id='address' /></div>

          <div>Year:<input required className='buttoncss2' type='text' placeholder='Year' id='date' /></div>

          <div>Quarter:<input required className='buttoncss2' type='number' placeholder='quarter' id='quarter' /> </div>
          {quarterError}
        </div>

        <div className='mar'>

          <div >
            Math:
            <input required className='buttoncss2' type='number' placeholder='Marks' id="subject1marks" />
          </div>
          <div>
            English:
            <input required className='buttoncss2' type='number' placeholder='Marks' id="subject2marks" />
          </div>

          <div>
            Science:
            <input required className='buttoncss2' type='number' placeholder='Marks' id="subject3marks" />
          </div>

          <div>
            Nepali:
            <input required className='buttoncss2' type='number' placeholder='Marks' id="subject4marks" />
          </div>
          <div>
            Grammer:
            <input required className='buttoncss2' type='number' placeholder='Marks' id="subject5marks" />
          </div>

          {/* <button className='buttoncss mt' placeholder='hello' onClick={resultpost}>Submit</button> */}
          <button className='buttoncss mt' placeholder='hello' type='submit' >submit</button>
        </div>
      </form>
    </Box>
  )
}

export default ResultUI